.modal {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%; }
  .modal .modal__content {
    display: inline-block; }
  .modal .overlay {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.5;
    z-index: -1; }



