.carousel-container {
  text-align: center;
  min-height: 210px;
  padding: 25px 0; }
  .carousel-container ul {
    padding: 0;
    margin: 0;
    list-style-type: none; }
  .carousel-container .carousel {
    position: relative; }
  .carousel-container .carousel__slide {
    margin-right: auto;
    margin-left: auto;
    display: none;
    width: 80%;
    list-style-type: none;
    text-align: center;
    max-width: 1200px; }
    .carousel-container .carousel__slide--active {
      display: block; }
  .carousel-container .carousel__arrow {
    position: absolute;
    top: 50%;
    display: block;
    color: #111;
    cursor: pointer;
    opacity: 0.75;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    -webkit-transition: opacity 0.15s cubic-bezier(0.4, 0, 1, 1);
    -o-transition: opacity 0.15s cubic-bezier(0.4, 0, 1, 1);
    transition: opacity 0.15s cubic-bezier(0.4, 0, 1, 1); }
    .carousel-container .carousel__arrow:focus {
      outline: 0; }
    .carousel-container .carousel__arrow:hover {
      opacity: 0.5; }
    .carousel-container .carousel__arrow--left {
      left: 5%; }
    .carousel-container .carousel__arrow--right {
      right: 5%; }
  .carousel-container .carousel__indicators {
    text-align: center;
    margin-top: 15px; }
    .carousel-container .carousel__indicators li {
      display: inline-block; }
      .carousel-container .carousel__indicators li:nth-of-type(n + 2) {
        margin-left: 9px; }
  .carousel-container .carousel__indicator {
    display: block;
    width: 24px;
    height: 4px;
    background-color: #111;
    cursor: pointer;
    opacity: .15;
    -webkit-transition: opacity 0.15s cubic-bezier(0.4, 0, 1, 1);
    -o-transition: opacity 0.15s cubic-bezier(0.4, 0, 1, 1);
    transition: opacity 0.15s cubic-bezier(0.4, 0, 1, 1); }
    .carousel-container .carousel__indicator:hover {
      opacity: .5; }
    .carousel-container .carousel__indicator--active, .carousel-container .carousel__indicator--active:hover {
      opacity: .75; }
  .carousel-container img {
    width: 100%;
    height: 450px;
    -o-object-fit: cover;
       object-fit: cover; }



